var input = document.getElementById("telephoneInput");

let inp = document.querySelector('#tel');

//  
if (inp !== null) {
    inp.addEventListener('focus', _ => {
        //      ,  
        if(!/^\+\d*$/.test(inp.value))
          //      
            inp.value = '+7';
    });
      
    inp.addEventListener('keypress', e => {
    //    
    if(!/\d/.test(e.key))
        e.preventDefault();
    });
}